import React from 'react';

const LogoutButton = () => {
    const socialLogout = () => {
        console.log('%c[INFO]   ...', 'color: blue');
        fetch('/api/member/logout', {
            method: 'POST',
        })
            .then(response => {
                if (response.ok) {
                    console.log('%c[INFO]   ', 'color: green');
                    window.location.href = "/api/member/login";  //     
                } else {
                    console.error('%c[ERROR]   :', 'color: red', response.status);
                }
            })
            .catch(error => {
                console.error('%c[ERROR]    :', 'color: red', error);
            });
    };

    return (
        <button onClick={socialLogout}>
            
        </button>
    );
};

export default LogoutButton;
